.form {
  $this: &;
  ._col {
    min-height: 30px; }
  &__title {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__captcha {
    @include flex(space-between, center);
    .input {
      margin-bottom: 0 !important;
      margin-right: 10px; } }
  &__field {
    @include notlast {
      margin-bottom: 1em; } }
  &__checkbox {
    font-size: $fz-sm;
    @include flex(space-between, center);
    @include notlast {
      margin-right: 25px; } }
  &__checkboxes {
    @include flex; }

  &--contacts {
    max-width: 775px;
    margin-left: auto;
    margin-right: auto;
    .input {
      @include notlast {
        margin-bottom: 1em; } } }
  &--order {
    border-top: 1px solid  $gray-light;
    padding-top: 25px;
    #{$this}__blocks {
      // margin-bottom: 30px
      // +md
 } } }      //   margin-bottom: 15px

.form-block {
  $this: &;
  @include flex;
  margin-bottom: 25px;
  @include md {
    margin-bottom: 15px; }
  @include xs {
    display: block; }
  &:last-child {
    #{$this}__content {
      border-bottom: 0; } }
  &__title {
    width: percentage(220/760);
    padding-right: 10px;
    @include xs {
      width: 100%;
      padding-right: 0;
      margin-bottom: 1em; } }
  &__content {
    width: percentage((760 - 220) / 760);
    border-bottom: 1px solid  $gray-light;
    @include xs {
      width: 100%; } } }

.input, .textarea {
  font-size: $fz-sm;
  color: $article-text;
  position: relative;
  input, textarea {
    background-color: $white-dark;
    border: 1px solid  $gray-light;
    height: 60px;
    padding: 19px 20px;
    width: 100%;
    @include tr(border-color .3s);
    @include placeholder {
      color: $gray; }
    @include sm {
      height: 50px;
      padding: 12px; }
    &:focus {
      border-color: $article-text; } }
  textarea {
    min-height: 125px; }
  &.has-text {
    .input__btn, .textarea__btn {
      display: flex; } }
  &__btn {
    position: absolute;
    z-index: 1;
    top: 50%;
    transform: translate(0, -50%);
    right: 15px;
    border-radius: 50%;
    width: 21px;
    height: 21px;
    @include flex(center, center);
    background-color: $gray-lightest;
    color: $accent;
    display: none;
    @include tr(color .3s);
    .icon {
      font-size: 10px; }
    &--inherit {
      background-color: transparent;
      width: auto;
      height: auto;
      color: inherit;
      @include hover {
        color: $accent; }
      .icon {
        font-size: 1.2em; } } }
  &--short {
    width: 260px; }
  &--full-h {
    height: 100%;
    input, textarea {
      height: 100% !important;
      min-height: 0;
      @include xs {
        min-height: 135px; } } }
  &--sm {
    input, textarea {
      height: 40px;
      padding: 7px 15px;
      @include sm {
        height: 35px;
        padding: 5px 10px; } } }
  &--search {
    input {
      padding-left: 50px;
      @include sm {
        padding-left: 40px; } }
    .input__clear {
      background-color: transparent;
      color: $c-text; } }
  &--btn-visible {
    .input__btn, .textarea__btn {
      display: flex; } } }
