.btn {
  @include inline-flex(center, center);
  background-color: $white;
  border: 1px solid  $white;
  font-size: $fz-sm;
  font-weight: 500;
  min-width: 160px;
  padding: .9em 1.5em;
  border-radius: 1.8em;
  text-align: center;
  color: $c-text;
  @include tr(background-color .3s, color .3s, border-color .3s);
  @include sm {
    padding: .8em; }
  @include hover {
    background-color: transparent;
    color: $white; }

  &--transp-accent {
    color: $accent;
    border-color: currentColor;
    background-color: transparent;
    @include hover {
      background-color: $accent;
      color: $white; } }
  &--full {
    min-width: 0;
    width: 100%; }
  &--square {
    background-color: $accent;
    border-color: $accent;
    color: $white;
    border-radius: 0;
    font-size: $fz;
    padding: 1.1em 1em;
    height: 60px;
    @include hover {
      color: $accent; }
    @include sm {
      height: 50px; } }
  &--text {
    background-color: $c-text;
    border-color: $c-text;
    @include hover {
      color: $c-text; } }
  &--face {
    background-color: #20538d;
    border-color: #20538d;
    @include hover {
      color: #20538d; } }
  &--sm {
    font-size: $fz-sm; }
  &--md {
    font-size: em(18); }
  &--reg {
    font-size: 1em;
    padding: .3em 1.5em;
    font-weight: 400; }
  &--black {
    background-color: $c-text;
    border-color: $c-text;
    @include hover {
      color: $c-text; } } }

.more-btn {
  color: $gray;
  @include inline-flex(flex-start, center);
  @include after {
    @include arr(8, 6, currentColor, b);
    display: inline-block;
    margin-left: .5em; }
  @include tr(color .3s);
  .cabinet-table-item__top.is-active & {
    color: $accent;
    @include after {
      @include arr(8, 6, currentColor, t); } } }
