.cabinet-top {
  @include flex(flex-start, center);
  @include xs {
    flex-wrap: wrap;
    align-items: flex-start; }
  &__img {
    @include cover;
    border-radius: 50%;
    background-color: $gray-lightest;
    overflow: hidden;
    width: 75px;
    height: 75px;
    flex: 0 0 75px; }
  &__content {
    flex-grow: 1;
    padding-right: 15px;
    padding-left: 25px;
    @include xs {
      flex: 0 0 calc(100% - 75px);
      padding-left: 15px;
      margin-bottom: 25px; } }
  &__title {
    small {
      font-size: em(16, 24); } }
  &__btn {
    @include xs {
      width: 100%; } } }

.cabinet-table {
  border: 1px solid  $gray-light;
  border-bottom: 0;
  &__top {
    @include flex(flex-start, center);
    background-color: $gray-lightest;
    border-bottom: 1px solid  $gray-light;
    padding-left: 20px;
    padding-right: 20px;
    position: sticky;
    top: $header-height;
    z-index: 2;
    @include lg {
      top: $header-height-lg; }
    @include sm {
      display: none; } }
  &__th {
    font-weight: 700;
    padding-top: .7em;
    padding-bottom: .7em;
    &--number {
      width: percentage(230 / 1410); }
    &--date {
      width: percentage(240 / 1410); }
    &--quantity {
      width: percentage(240 / 1410); }
    &--status {
      width: percentage(240 / 1410); }
    &--price {
      width: percentage(240 / 1410); }
    &--btn {
      width: percentage(220 / 1410); } } }


.cabinet-table-item {
  $this: &;
  &__top {
    @include flex(flex-start, center, row, wrap);
    width: 100%;
    text-align: left;
    padding-left: 20px;
    padding-right: 20px;
    border-bottom: 1px solid  $gray-light; }
  &__td {
    padding-top: .7em;
    padding-bottom: .7em;
    padding-right: .7em;
    display: block;
    @include sm {
      width: 33.33333%; }
    @include xs {
      width: 50%; }
    &--number {
      width: percentage(230 / 1410); }
    &--date {
      width: percentage(240 / 1410);
      .icon {
        font-size: .8em; } }
    &--quantity {
      width: percentage(240 / 1410); }
    &--status {
      width: percentage(240 / 1410); }
    &--price {
      width: percentage(240 / 1410); }
    &--btn {
      width: percentage(220 / 1410); } }
  &__content {
    background-color: $white;
    @include flex;
    max-height: 0;
    overflow: hidden;
    @include sm {
      display: block; }
    &.is-active {
      visibility: visible;
      max-height: none; } }
  &__content-left {
    width: percentage(820 / 1410);
    border-right: 1px solid $gray-light;
    padding-left: 25px;
    padding-right: 25px;
    padding-bottom: 25px;
    @include md {
      padding-left: 15px;
      padding-right: 15px; }
    @include sm {
      width: 100%;
      border-right: 0; } }
  &__content-right {
    width: percentage((1440 - 820) / 1420);
    padding-left: 45px;
    padding-right: 45px;
    padding-top: 25px;
    padding-bottom: 25px;
    @include lg {
      padding-left: 30px;
      padding-right: 30px; }
    @include md {
      padding-left: 15px;
      padding-right: 15px; }
    @include sm {
      width: 100%;
      padding-top: 0; } } }

.cabinet-table-meta {
  &__title {
    @include flex(flex-start, center);
    margin-bottom: 45px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; }
    .title {
      margin-right: .5em; } }
  &__list {
    margin-bottom: 20px; }
  &__btn {
    max-width: 330px;
    @include xs {
      max-width: none; } } }

.cabinet-table-meta-list {
  @include clr;
  &__title {
    float: left;
    color: $gray;
    margin-right: .5em;
    &--block {
      float: none; } }
  &__text {
    &--block {
      margin-bottom: 10px; } } }

.status {
  @include inline-flex(flex-start, center);
  .icon {
    margin-right: .4em; }
  &--done {
    color: #6abb46; }
  &--processing {
    color: #f19e1b; }
  &--transporting {
    color: #2570cc; } }
