.product-descr {
  margin-bottom: 40px;
  @include lg {
    margin-bottom: 30px; }
  @include md {
    margin-bottom: 15px; }
  &__top {
    @include flex(space-between, center);
    margin-bottom: 10px;
    small {
      font-size: $fz-sm;
      font-weight: 500; } }
  &__title {
    margin-bottom: 10px; }
  &__block {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__block-title {
    color: $gray;
    margin-bottom: 5px; }
  &__btns {
    @include flex;
    margin-top: 50px;
    margin-bottom: 50px;
    @include xl {
      margin-top: 30px;
      margin-bottom: 30px; }
    @include md {
      margin-top: 15px;
      margin-bottom: 15px; } }
  &__btn-lg {
    // width: calc(100% - 95px)
    flex-grow: 1;
    padding-right: 1em;
    @include xs {
      padding-right: .5em; } }
  &__btn-sm {
    flex: 0 0 95px;
    @include xs {
      flex: 0 0 75px; } }
  &__text {
    p {
      margin-bottom: 30px;
      @include md {
        margin-bottom: 15px; } } } }

.colors, .sizes {
  @include flex(flex-start, normal, row, wrap); }

.color {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: block;
  @include notlast {
    margin-right: 20px;
    margin-bottom: 20px;
    @include md {
      margin-right: 10px;
      margin-bottom: 10px; } } }

.size {
  min-width: 40px;
  height: 40px;
  @include flex(center, center);
  border: 1px solid  $gray;
  text-transform: uppercase;
  @include notlast {
    margin-right: 7px;
    margin-bottom: 7px; } }

.product-meta-block {
  &__title {
    @extend .filters-block__title;
    border-bottom: 1px solid  $gray-light; }
  &__title-icon {
    @extend .filters-block__title-icon; }
  &__content {
    @extend .filters-block__content; } }

.product-preview {
  // +flex(flex-start, normal, row, wrap)
  margin-left: -.5em;
  margin-right: -.5em;
  @include clr; }


.product-img {
  display: block;
  float: left;
  width: 50%;
  position: relative;
  padding-left: .5em;
  padding-right: .5em;
  margin-bottom: 1em;
  &__img {
    width: 100%;
    padding-top: 120%;
    @include cover;
    // background-repeat: no-repeat
    // background-position: 50% 100%
 } }    // background-size: contain


