.article {
  $this: &;
  font-size: em(21);
  color: $article-text;
  h1, h2, h3, h4, h5, h6, p, article, img, ul, ol {
    @include notlast {
      margin-bottom: 40px;
      @include xl {
        margin-bottom: 30px; }
      @include md {
        margin-bottom: 15px; } } }
  h1, h2, h3, h4, h5, h6 {
    color: $c-text; }
  h2, h1 {
    @extend .title;
    @include css-lock(28, 55, 320, 1399, font-size); }
  img {
    width: 100%; }
  h2 {
    font-weight: 700; }
  h3 {
    @extend .title;
    font-size: 1em; }
  article {
    max-width: 875px;
    margin-left: auto;
    margin-right: auto; }
  ol {
    @include counter('-text', '.');
    li {
      &::before {
        display: inline-block;
        margin-right: .5em; } } }
  ul {
    li {
      &::before {
        content: '-';
        display: inline-block;
        margin-right: .5em; } } }
  li {
    @include notlast {
      margin-bottom: 40px;
      @include xl {
        margin-bottom: 30px; }
      @include md {
        margin-bottom: 15px; } } }
  a:not(.btn) {
    color: $blue;
    text-decoration: underline; }
  blockquote {
    color: $c-text;
    font-weight: 700; }
  table {
    text-align: center;
    td, th {
      padding: 10px;
      border: 1px solid  $gray-light; }
    th {
      font-weight: 400;
      color: $black; }
    td {
      font-size: $fz;
      @include sm {
        font-size: $fz-sm-px; } } }
  .btn {
    font-size: $fz-sm-px; }
  &__subttl {
    color: $c-text;
    margin-bottom: 40px;
    @include xl {
      margin-bottom: 30px; }
    @include lg {
      br {
        display: none; } }
    @include md {
      margin-bottom: 15px; } }
  &__date {
    color: $gray;
    font-size: em(24, 21);
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }

  &--page {
    font-size: $fz;
    text-align: justify;
    h1, h2, h3, h4, h5, h6 {
      text-align: left; }
    h1 {
      @extend .title;
      @include css-lock(28, 55, 320, 1399, font-size);
      font-weight: 700; }
    h2 {
      @include css-lock(24, 32, 320, 1399, font-size);
      font-weight: 500; }
    h3 {
      @include css-lock(18, 21, 320, 1399, font-size); }
    article {
      max-width: none;
      margin-left: 0;
      margin-right: 0;
      @include notlast {
        margin-bottom: 75px;
        @include xl {
          margin-bottom: 50px; }
        @include lg {
          margin-bottom: 30px; }
        @include md {
          margin-bottom: 15px; } } }
    ul {
      li {
        @include list-bullet; } }
    ol {
      @include counter('-text', ')'); }
    li {
      @include notlast {
        margin-bottom: 0 !important; } } } }

.article-features {
  $this: &;
  &__item {
    margin-bottom: 35px;
    @include lg {
      margin-bottom: 25px; }
    @include md {
      margin-bottom: 15px; } }

  &--cols {
    @include flex(flex-start, normal, row, wrap);
    margin-left: -10px;
    margin-right: -10px;
    #{$this}__item {
      padding-left: 10px;
      padding-right: 10px;
      width: 60%;
      @include md {
        width: 100%; }
      &:nth-child(odd) {
        width: 40%;
        @include md {
          width: 100%; } } } } }


.article-feature {
  &__icon {
    color: $accent;
    margin-bottom: 8px;
    .icon {
      font-size: em(54, 21); } } }

.article-meta {
  background-color: $gray-lightest;
  padding-top: 50px;
  padding-bottom: 50px;
  margin-bottom: 40px;
  margin-top: 40px;
  @include xl {
    padding-top: 40px;
    padding-bottom: 40px;
    margin-bottom: 30px;
    margin-top: 30px; }
  @include lg {
    padding-top: 30px;
    padding-bottom: 30px; }
  @include md {
    padding-top: 15px;
    padding-bottom: 15px;
    margin-bottom: 15px;
    margin-top: 15px; }
  &__items {
    @include flex(flex-start, normal, row, wrap); } }

.article-meta-item {
  @include flex(flex-start, center);
  color: $c-text;
  font-weight: 500;
  width: 50%;
  padding-right: 10px;
  margin-bottom: 40px;
  @include lg {
    margin-bottom: 30px; }
  @include md {
    margin-bottom: 15px;
    width: 100%; }
  &__nmb {
    @extend .title;
    @include css-lock(36, 70, 320, 1399, font-size);
    text-transform: uppercase;
    color: $accent;
    margin-right: .2em; } }

.article-partners {
  @include flex(flex-start, normal, row, wrap);
  padding-left: -10px;
  padding-right: -10px;
  &__item {
    width: 25%;
    // margin-bottom: 30px
    @include flex(center, center);
    padding: 10px;
    @include xs {
      width: 33.33333%; }
    img {
      max-height: 80px;
      width: auto; } } }
