.header {
  &.is-relative {
    .popup {
      position: absolute;
      top: 0;
      right: 0;
      left: auto;
      margin-left: 0 !important;
      margin-top: 0 !important;
      top: calc(100% + 31px);
      @include lg {
        top: calc(100% + 17px); } } } }

.overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba($black, .7); }

.popup {
  position: fixed;
  width: 465px;
  margin-bottom: -$header-height;
  left: 50%;
  top: 50%;
  // transform: translate(-50%, -50%)
  z-index: 10;
  background-color: $white;
  border-radius: 10px;
  overflow: hidden;
  @include hidden;
  @include ready {
    @include tr(opacity .3s, visibility .3s); }
  @include xs {
    width: auto;
    left: 15px;
    right: 15px;
    top: 15vh; }
  &.is-active {
    @include visible; }
  &__wrap {
    @extend .overlay;
    @include vertical;
    overflow: auto;
    padding: 15px;
    padding-top: $header-height + 15px;
    @include hidden;
    @include ready {
      @include tr(opacity .3s, visibility .3s); }
    &.is-active {
      @include visible;
      .popup {
        @include visible; } }
    .popup {
      display: inline-block;
      vertical-align: middle;
      position: relative;
      top: auto;
      bottom: auto;
      left: auto;
      right: auto;
      font-size: $fz;
      @include sm {
        font-size: $fz-sm-px; } } }
  &__top {
    background-color: $gray-lightest;
    padding-top: 25px;
    padding-bottom: 25px;
    padding-left: 38px;
    padding-right: 60px;
    @include xl {
      padding-left: 30px;
      padding-right: 30px; }
    @include lg {
      padding-left: 20px;
      padding-right: 20px; }
    @include md {
      padding: 15px; } }
  &__close {
    position: absolute;
    top: 25px;
    right: 38px;
    @include xl {
      right: 30px; }
    @include lg {
      right: 20px; }
    @include md {
      right: 15px;
      top: 15px; } }
  &__inner {
    padding: 50px 38px 55px;
    text-align: center;
    @include xl {
      padding: 50px 30px; }
    @include lg {
      padding: 40px 20px; }
    @include md {
      padding: 30px 15px; }
    @include sm {
      padding: 20px 15px; } }
  &__form {
    @include notlast {
      margin-bottom: 50px;
      @include xl {
        margin-bottom: 40px; }
      @include lg {
        margin-bottom: 30px; }
      @include md {
        margin-bottom: 15px; } } }
  &__content-title {
    font-size: $fz-sm;
    margin-bottom: 12px;
    @include md {
      margin-bottom: 6px; } }
  &__content-link {
    display: inline-block;
    color: $accent;
    font-size: $fz-sm;
    margin-bottom: 30px;
    font-weight: 500;
    @include md {
      margin-bottom: 15px; }
    @include hover {
      text-decoration: underline; } }
  &__btn {
    @include notlast {
      margin-bottom: 15px; } }
  &__rating {
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } } }
